<template>
  <v-card flat>
    <v-card-text>
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="data"
        item-key="no_order"
        show-select
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-menu bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  :loading="loading"
                  :disabled="selected.length === 0 ? true : loading"
                  v-on="on"
                >
                  Actions
                </v-btn>
              </template>
              <v-list>
                <v-list-item @click="postStatus(3)">
                  <v-list-item-title>
                    Move to Picked Up
                  </v-list-item-title>
                </v-list-item>
                <v-list-item @click="postStatus(4)">
                  <v-list-item-title>
                    Move to Delivery Assigned
                  </v-list-item-title>
                </v-list-item>
                <v-list-item @click="postStatus(5)">
                  <v-list-item-title>
                    Move to Delivered
                  </v-list-item-title>
                </v-list-item>
                <v-list-item @click="postStatus(6)">
                  <v-list-item-title>
                    Move to Canceled
                  </v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
            <v-divider
              class="mx-4"
              inset
              vertical
            />
            <v-text-field
              v-model="search"
              left
              class="mx-2"
              append-icon="mdi-magnify"
              label="Search"
              align-right
            />
          </v-toolbar>
        </template>

        <template v-slot:item.actions="{ item }">
          <v-btn
            icon
            small
            @click="detailOrder(item.no_order)"
          >
            <v-icon>mdi-eye</v-icon>
          </v-btn>
        </template>
        <template v-slot:item.delivery_fee="{ item }">
          {{ formatThousands(item.delivery_fee, { separator: ',' }) }}
        </template>
      </v-data-table>
    </v-card-text>
    <detail-order-dialog
      v-show="this.$store.state.dialog"
      :data="detail"
    />
  </v-card>
</template>

<script>
  import axios from 'axios'
  import DetailOrderDialog from './DetailOrderDialog.vue'
  var formatThousands = require('format-thousands')
  export default {
    components: {
      DetailOrderDialog,
    },
    // eslint-disable-next-line vue/require-prop-types
    props: ['data'],
    data () {
      return {
        formatThousands,
        selected: [],
        search: '',
        dialog: false,
        dialogField: false,
        sortable: false,
        loading: false,
        detail: [],
        headers: [
          {
            text: 'No Order',
            value: 'no_order',
          },
          {
            text: 'Client',
            value: 'client',
          },
          {
            text: 'Delivery Fee',
            value: 'delivery_fee',
          },
          {
            text: 'Order Status',
            value: 'order_status',
          },
          // {
          //   text: 'Payment Status',
          //   value: 'payment_status',
          // },
          {
            text: 'Payment Method',
            value: 'payment_method',
          },
          {
            text: 'Actions',
            value: 'actions',
            sortable: false,
          },
        ],
      }
    },

    methods: {
      postStatus (status) {
        this.loading = true
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            status: status,
          }),
        )
        axios.post('admin/status', data).then(() => {
          this.refresh()
          this.selected = []
          this.loading = false
        })
      },

      refresh () {
        axios.get('admin/order/canceled').then(res => {
          this.data = res.data.data
        })
      },

      detailOrder (no) {
        // eslint-disable-next-line camelcase
        const noOrder = no.replace(/#/g, '')
        console.log(noOrder)
        axios.get('admin/order/detail/' + noOrder).then(res => {
          this.detail = res.data.data[0]
          this.$store.commit('dialog', true)
        })
      },
    },
  }
</script>
